import Swiper from "swiper";
import { Pagination, EffectFade } from "swiper/modules";
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/effect-fade';

let swiperInstance = null;
const viewAllBtn = document.querySelector('.gallery-btn');
const galleryItems = Array.from(document.querySelectorAll('.gallery-list .gallery-item'));
const step = 2;
let visibleCount = step;
let isExpanded = false; //Ñ‡Ð¸ Ñ€Ð¾Ð·Ð³Ð¾Ñ€Ð½ÑƒÑ‚Ð° Ð³Ð°Ð»ÐµÑ€ÐµÑ
console.log(galleryItems);


function initSwiperIfMobile() {
const isMobile = window.innerWidth <= 767;
if (isMobile && !swiperInstance) {
  swiperInstance = new Swiper('.swiperGallery', {
    modules: [Pagination, EffectFade],
      effect: "fade",
      fadeEffect: {
      crossFade: true,
      },
      pagination: {
      el: ".swiper-pagination",
      clickable: true,},
      loop: true,
      });
      galleryItems.forEach(item => item.classList.remove('hidden'));
      viewAllBtn.style.display = 'none';}
      else
        if (!isMobile && swiperInstance) {
          swiperInstance.destroy(true, true);
          swiperInstance = null;
        }
        console.log (galleryItems);
    }

function showImages() {
  if (window.innerWidth > 767) {
       galleryItems.forEach((item, index) => {
       if 
       (index < visibleCount) { 
         // ÐŸÐ¾ÑÐ²Ð° Ñ„Ð¾Ñ‚Ð¾ Ð¿Ð¾ Ñ‡ÐµÑ€Ð·Ñ– Ð²Ñ–Ð´ Ð¿ÐµÑ€ÑˆÐ¾Ð³Ð¾
          if (item.classList.contains('hidden')) {
              setTimeout(() => {
              item.classList.remove('hidden');
              },
        (index % step) * 150);
        console.log (galleryItems)
       }
      }
      else {
         // Ð—Ð½Ð¸ÐºÐ½ÐµÐ½Ð½Ñ Ñ„Ð¾Ñ‚Ð¾ Ð¿Ð¾ Ñ‡ÐµÑ€Ð·Ñ– Ð· ÐºÑ–Ð½Ñ†Ñ
          if (!item.classList.contains('hidden')) {
              const delayIndex = (galleryItems.length - 1 - index) % step;
              setTimeout(() => {
              item.classList.add('hidden');
              }, 
              delayIndex * 150);
       }
      }
      });

      if (visibleCount >= galleryItems.length) {
        viewAllBtn.textContent = 'Show less'
        isExpanded = true;
      }
      else {
        viewAllBtn.textContent = 'View all';
        isExpanded = false;
      }
      viewAllBtn.style.display = 'block';
  }
  
}

function handleButtonClick() {
  if (!isExpanded) {
    const previousVisibleCount = visibleCount;
    visibleCount += step;
    if (visibleCount >= galleryItems.length) {
      visibleCount = galleryItems.length;
    }

    showImages();

    // ÐŸÑ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ° Ð´Ð¾ Ð¿ÐµÑ€ÑˆÐ¾Ð³Ð¾ Ð½Ð¾Ð²Ð¾Ð³Ð¾ Ð²Ñ–Ð´ÐºÑ€Ð¸Ñ‚Ð¾Ð³Ð¾ Ñ„Ð¾Ñ‚Ð¾
    const firstNewItemIndex = previousVisibleCount;
    const firstNewItem = galleryItems[firstNewItemIndex];

    if (firstNewItem) {
      const itemTop = firstNewItem.getBoundingClientRect().top + window.pageYOffset;
      const offset = 150;

      window.scrollTo({
        top: itemTop - offset,
        behavior: 'smooth'
      });
    }
  } else {
    visibleCount = step;

    // ÐŸÑ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ° Ð´Ð¾ Ð¿ÐµÑ€ÑˆÐ¾Ð³Ð¾ Ñ„Ð¾Ñ‚Ð¾ Ð¿Ñ€Ð¸ "ÐŸÐ¾ÐºÐ°Ð·Ð°Ñ‚Ð¸ Ð¼ÐµÐ½ÑˆÐµ"
    const firstItem = galleryItems[0];
    const itemTop = firstItem.getBoundingClientRect().top + window.pageYOffset;
    const offset = 120;

    window.scrollTo({
      top: itemTop - offset,
      behavior: 'smooth'
    });

    showImages();
  }
}





document.addEventListener ('DOMContentLoaded', () => {
    initSwiperIfMobile ();
    showImages ();
    viewAllBtn.addEventListener('click', handleButtonClick)
});

window.addEventListener('resize', () => {
    initSwiperIfMobile();
    if (!swiperInstance) {
      showImages();
    }
})
   